main {
    width: 100vw;
    padding: 20px;
}
.item-container-header {
    background: teal;
    color: #fff;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 10px 10px 0 0 ;
}
.item-container {
    display: flex;
    align-items: center;
    justify-content: space-around;
    height: 320px;
}
.item-container-header, .item-container {
    width: 95%;
    margin: 0 auto;
}
.products-square {
 width: 190px;
 height: 210px;
 border-radius: 5px;
}

.items {
    border-radius: 5px;
    display: flex;
    width: 200px;
    height: 300px;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    padding: 5px;
}
.items:hover {
    box-shadow: 3px 3px 5px #333;
    transform: scale(1.05);
}
.cart-btn {
    background: #c4d6b0;
    padding: 4px;
    text-align: center;
    border: none;
    outline: none;
    border-radius: 5px;
    cursor: pointer;
    width: 90%;
    height: 30px;
    display: block;
    margin: 0 auto;
}
.info {
    width: 100%;
}
.info p {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}