#hero-section {
    width: 100vw;
    height: 40%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    padding-block: 20px;
    background-color: teal;
}
.side-bar {
   width: 270px;
   padding: 10px;
   box-shadow: 3px 4px 5px #333;
   background: #fff;
   border-radius: 5px; 
}
.side-bar ul li {
    display: flex;
    align-items: center;
    margin-block: 3px;
    border-bottom: 1px solid #333;
}
.side-bar ul li:hover {
    background-color: steelblue;
}
.icons {
    display: inline-block;
    margin: 5px 4px;
}

#banner-container {
    display: flex;
    gap: 5px;
    overflow-x: scroll;
    overflow-y: hidden;
    width: 50%;
}
.banners, .ads {
    height: auto;
    max-width: 100%;
    border-radius: 5px;
}
.ads:hover {
transform: scale(1.05);
transition: 0.5s ease-in;
}
#ads-container {
    width: 200px;
}
@media (max-width :1000px) {
    .side-bar {
        position: absolute;
        left: -280px;
        transition: left 0.5s ease-in;
    }
    .show-side-bar {
        position: absolute;
        left: 0;
    }
    #banner-container {
        width: 70%;
    }
    #ads-container {
        width: 160px;
    }
    .side-bar {
        padding: 5px;
    }
    .side-bar ul li {
        margin-block: 2px;
    }
}
@media (max-width :780px) {
    #hero-section {
        flex-direction: column;
    }
    #ads-container {
        display: flex;
        margin-top: 10px;
        width: 90%;
        gap: 10px;
        justify-content: center;
        align-items: center;
    } 
    #banner-container {
        width: 90%;
    }
    .side-bar {
        position: absolute;
        top: 122px;
        left: -280px;
        transition: left 0.5s ease-in;
    }
    .show-side-bar {
        position: absolute;
        left: 0;
    }
    .ads {
        width: 50%;
        border-radius: 7px;
    }
}

