
#navbar {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    height: 60px;
    background: #c4d6b0;
}
.icons {
    width: 30px;
    height: 30px;
    color: beige;
}
#logo {
    font-family: "Quicksand", sans-serif;
    font-optical-sizing: auto;
    font-weight: 800;
    font-style: normal;
    font-size: 25px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 120px;
}

form {
padding: 5px;
width: 35%;
display: flex;
justify-content: space-evenly;
}
#search {
    width: 85%;
    padding: 3px;
    border: none;
    outline: none;
    border-radius: 3px;
    height: 35px;
}
.btn {
    text-align: center;
    font-size: 13px;
    padding: 5px;
    background: #377771;
    color: #fff;
    border-radius: 5px;
    border: none;
    outline: none;
    cursor: pointer;
}

#links ul  {
    display: flex;
    justify-content: space-around;
    align-items: center;
    list-style: none;
}
ul li a{
    display: flex;
    align-items: center;
    margin: 0 10px;
    color: #333;
    text-decoration: none;
}
#desktop-nav {
    display: flex;
    justify-content: space-evenly;
    width: 100%;
    align-items: center;
}
#mobile-nav {
    display: none;
}
.menu-icon {
  display: none;
}
@media (max-width :900px) {
   .menu-icon {
    display: block;
   }
}
@media (max-width: 780px) {
  #mobile-nav {
    padding-top: 10px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: column;
    width: 100%;
    height: 100%;
  }
  #desktop-nav {
    display: none;
  }
  #mobile-form {
    width: 100%;
  }
  .mobile-links {
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
  }
  .mobile-links ul {
    display: flex;
    align-items: center;
    justify-content: space-around;
    list-style: none;
  }
  #navbar {
    height: 120px;
  }
  #mobile-form {
    display: flex;
    align-items: center;
    justify-content: space-around;
    transform: translateY(-10px);
    width: 70%;
    padding: 3px;
  }
  #search {
    width: 85%;
  }
  .btn {
    height: 40px;
    padding: 2px;
  }
}
@media (max-width :780px) {
  #mobile-form {
    width: 95%;
  } 
}